/*=============== GOOGLE FONTS ===============*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

/*=============== VARIABLES CSS ===============*/
:root {
  --header-height: 3rem;

  /*========== Colors ==========*/
  /* soft blue-gray hue */
  --hue: 220;
  --sat: 8%;

  /* Text Colors */
  --text-color: hsl(var(--hue), var(--sat), 85%);
  --title-color: hsl(var(--hue), var(--sat), 96%);
  --title-color-dark: hsl(var(--hue), var(--sat), 98%);

  /* Backgrounds */
  --body-color: hsl(var(--hue), var(--sat), 10%);
  --container-color: hsl(var(--hue), var(--sat), 17%);

  /* Accent for buttons & links */
  --accent: hsl(45, 100%, 50%);

  /*========== Font and typography ==========*/
  --body-font: "Poppins", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;

  /*.5rem = 8px | 1rem = 16px ...*/
  --h1-font-size: clamp(1.8rem, 4vw, 2.25rem);
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --big-font-size: 3.5rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  /*========== Font weight ==========*/
  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margenes Bottom ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;

  /*========== Breakpoints ==========*/
  --bp-xs: 575px;
  --bp-sm: 767px;
  --bp-md: 991px;
  --bp-lg: 1199px;
  --bp-xl: 1399px;
  --bp-xxl: 1599px;

  /*========== Rounder ==========*/
  --round-size: 13px;
}

/*=============== BASE ===============*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body {
  line-height: 1.6;
  background-color: var(--body-color);
  color: var(--text-color);
}

h1,
h2,
h3 {
  line-height: 1.2;
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*=============== REUSABLE CSS CLASSES ===============*/
.section {
  padding: 5rem 0;
}

.section__title {
  font-size: var(--h1-font-size);
  color: var(--accent);
}

.section__subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: 4rem;
}

.section__title,
.section__subtitle {
  text-align: center;
}

/*=============== LAYOUT ===============*/
.container {
  max-width: 90vw;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

/*=============== BUTTONS ===============*/
.button {
  display: inline-block;
  background-color: var(--title-color);
  color: var(--container-color);
  padding: 1.25rem 2rem;
  border-radius: 1rem;
  font-weight: var(--font-medium);
}

.button:hover {
  background-color: var(--accent);
}

.button__icon {
  margin-left: var(--mb-0-5);
}

.button--flex {
  display: inline-flex;
  align-items: center;
}

/*=============== CUSTOM SCROLL BAR ===============*/
::-webkit-scrollbar {
  width: 0.5rem;
}

::-webkit-scrollbar-track {
  background: var(--container-color);
}

::-webkit-scrollbar-thumb {
  background: var(--title-color);
}

button,
.button {
  transition: background-color .3s ease, color .3s ease;
}

::-webkit-scrollbar-thumb {
  transition: background-color .3s;
}

:focus-visible {
  outline: 1px solid var(--accent);
  outline-offset: 4px;
}

/*=============== BREAKPOINTS ===============*/
/* Responsive typography */
@media screen and (max-width: 1199px) {
  :root {
    --big-font-size: 2.75rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/* For large devices */
@media screen and (max-width: 1199px) {

  .button {
    padding: 1rem 1.75rem;
  }

  .button__icon {
    width: 22px;
    height: 22px;
  }
}

/* For medium devices */
@media screen and (max-width: 767px) {
  body {
    margin: 0 0 var(--header-height) 0;
  }

  .section {
    padding: 3rem 0;

  }

  .section__subtitle {
    margin-bottom: var(--mb-3);
  }
}

/* For small devices */
@media screen and (max-width: 767px) {
  :root {
    --big-font-size: 2.25rem;
  }

}

/* XXL (extra extra large) Devices */
@media screen and (max-width: 1599px) {
  :root {
    --accent: hsl(45, 100%, 50%);

    --h1-font-size: 2.1rem;
    --h2-font-size: 1.4rem;
    --h3-font-size: 1.2rem;
  }
}

/* XL (extra large) Devices */
@media screen and (max-width: 1399px) {
  :root {
    --accent: hsl(343, 79%, 58%);

    --h1-font-size: 2rem;
    --h2-font-size: 1.35rem;
    --h3-font-size: 1.15rem;
  }
}

/* LG (large) Devices */
@media screen and (max-width: 1199px) {
  :root {
    --accent: hsl(202, 66%, 39%);

    --h1-font-size: 1.9rem;
    --h2-font-size: 1.3rem;
    --h3-font-size: 1.1rem;
  }
}

/* MD (medium) Devices */
@media screen and (max-width: 991px) {
  :root {
    --accent: hsl(158, 49%, 39%);

    --h1-font-size: 1.75rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1.05rem;
    --normal-font-size: 0.95rem;
  }
}

/* SM (small) Devices */
@media screen and (max-width: 767px) {
  :root {
    /* --accent: hsl(0, 0%, 100%); */

    --h1-font-size: 1.6rem;
    --h2-font-size: 1.2rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.9rem;
  }
}

/* XS (extra small) Devices */
@media screen and (max-width: 575px) {
  :root {
    /* --accent: hsl(249, 79%, 60%); */

    --h1-font-size: 1.45rem;
    --h2-font-size: 1.15rem;
    --h3-font-size: 0.95rem;
    --normal-font-size: 0.875rem;
  }
}