.skills__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

/* Card */
.skills__card {
  border-radius: 12px;
  padding: 0;
  overflow: hidden;
  background: var(--container-color);
  transition: box-shadow 0.25s ease;
}

.skills__card.open {
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
}

/* Header (Accordion) */
.skills__header {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.2rem 1.4rem;
  background: transparent;
  border: none;
  cursor: pointer;
}

.skills__title {
  font-size: 1.25rem;
  font-weight: 600;
}

/* Content */
.skills__content {
  padding: 0 1.4rem 1.4rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.8rem;
}

/* Skill item */
.skills__item {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.skills__icon {
  width: 28px;
  height: 28px;
  object-fit: contain;
}

.skills__icon-default {
  font-size: 28px;
  color: var(--first-color);
}

/* Text */
.skills__name {
  font-weight: 600;
  margin: 0;
}

.skills__level {
  font-size: 0.85rem;
  opacity: 0.7;
}

/*=============== BREAKPOINTS ===============*/
/* XXL (extra extra large) Devices */
@media screen and (max-width: 1599px) {}

/* XL (extra large) Devices */
@media screen and (max-width: 1399px) {}

/* LG (large) Devices */
@media screen and (max-width: 1199px) {}

/* MD (medium) Devices */
@media screen and (max-width: 991px) {}

/* SM (small) Devices */
@media screen and (max-width: 767px) {}

/* XS (extra small) - Devices */
@media screen and (max-width: 575px) {
  .skills__container {
    grid-template-columns: 1fr;
  }

  .skills__content {
    grid-template-columns: 1fr;
  }
}